      .gHeader .gHeader__gNavi ul.gHeader__gNavi__inner li.active .gHeader__gNavi__submenu {
        padding-bottom: 8px; }
        .gHeader .gHeader__gNavi ul.gHeader__gNavi__inner li.active .gHeader__gNavi__submenu a {
